<template>
	<view class="page" :data-weui-theme="theme">
		<!-- <scroll-view class="G_SC_V" style="'height:' + mHeight + 'px'" lower-threshold="100" refresher-threshold="50" refresher-enabled="true" :refresher-triggered="isMoveToUp" @refresherrefresh="bindLoadingNew" @scrolltolower="bindLoadingMore" enable-flex="true" scroll-y="true"> -->
		<scroll-view class="G_SC_V" style="'height:' + mHeight + 'px'" enable-flex="true" scroll-y="true">
			<a class="weui-media-box weui-media-box_appmsg" style="height:100px;">
				<view class="page__hd">
					<view class="page__title" style="margin-left: -50rpx;">我发布的任务测试LM</view>
					<view class="page__desc" style="margin-left: -50rpx;">编辑、发布、分发</view>
				</view>
				<view class="pm-TC-new" id="AddNewPO" style="margin-left:30px;">
					<view class="pm-TC-Txt" style="margin-top: -100rpx;">创建新任务</view>
					<!--<image src></image>-->
					<view class="pm-TC-progress__bar">
						<view class="pm-TC-progress__inner-bar js_progress" style="width: 0%;"></view>
					</view>
				</view>
			</a>
			<view class="weui-panel weui-panel_access">
				<view class="weui-panel__hd">交付任务列表</view>
				<view class="weui-panel__hd stateselect_panel">
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImageEdit"
								:src="ICOImage.ICOImageEdit"></image>
						</view>
						<view>编辑中</view>
					</view>
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImagePublish"
								:src="ICOImage.ICOImagePublish"></image>
						</view>
						<view>发布</view>
					</view>
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImageWaitAuthorize"
								:src="ICOImage.ICOImageWaitAuthorize"></image>
						</view>
						<view>待授权</view>
					</view>
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImageEXEC"
								:src="ICOImage.ICOImageEXEC"></image>
						</view>
						<view>执行中</view>
					</view>
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImageUnaccepted"
								:src="ICOImage.ICOImageUnaccepted"></image>
						</view>
						<view>待验收</view>
					</view>
					<view class="stateselect_item">
						<view>
							<image style="width:25px;height:25px;" v-if="ICOImage.ICOImageAccept"
								:src="ICOImage.ICOImageAccept"></image>
						</view>
						<view>已验收</view>
					</view>
				</view>
				<view class="weui-panel__bd">
					<block v-for="(PMCOI, index) in PMCOIList" :key="index" v-if="PMCOIList">
						<view class="ColumnBox">
							<a class="weui-media-box weui-media-box_appmsg">
								<view class="weui-media-box__hd" style="width:86px;height:70px;" :id="PMCOI._id"
									@tap="onPMCOICommandSelect" @longpress="onPMCOIImageTap">
									<image v-if="PMCOI.ZCTProductURL" class="weui-media-box__thumb"
										style="width:70px;height:70px;" :src="PMCOI.ZCTProductURL" alt
										mode="aspectFill"></image>
								</view>
								<view class="pm-task-box__bd" :id="PMCOI._id" @tap="onPMCOICommandSelect"
									@longpress="onPMCOITap" style="width:100%;">
									<View class="taskdetail_title">
										<view class="taskName">{{PMCOI.PMInnerProductName}}<a
												class="taskNum">{{PMCOI.PMOrderNum}}{{PMCOI.PMOrderUnit}}</a></view>
									</View>
									<view class="pm-taskdetail">
										<view class="pm-taskdetail__desc">
											已完工{{PMCOI.ZCTFinishedNum}}{{PMCOI.PMOrderUnit}},计划交付时间：{{PMCOI.ZCTPlanDeliDateShort}}
										</view>
										<view class="pm-taskstate">
											<view class="pm-taskstate-time">{{PMCOI.calChangeTimeStr}}</view>
											<view class="pm-taskstate-state">{{PMCOI.calPMState}}</view>
										</view>
									</view>
								</view>
							</a>
							<view class="task_progressGroup">
								<block v-for="(PMCOIStates, index2) in PMCOI.calTaskStates" :key="index2"
									v-if="PMCOI.calTaskStates">
									<view class="task_progress">

										<view class="task_progressSubTxt">
											{{PMCOIStates.calStageName}}
										</view>

										<view class="task_progressSub" :style="PMCOIStates.calStageStyle">
										</view>

									</view>

								</block>

							</view>

						</view>

					</block>
					<view style="height:20px"></view>
				</view>
			</view>
			<view v-if="isMoveToDown">
				<view class="weui-loading__wrp">
					<view class="loading wx_dot_loading"></view>
				</view>
			</view>
		</scroll-view>

		<view class="fadeIn" v-if="propertyDialog.isPropertyDialogOpen">
			<view class="weui-mask" @tap="propertyDialogClose"></view>
			<view class="weui-half-screen-dialog">

				<view class="weui-half-screen-dialog__hd">
					<view class="weui-half-screen-dialog__hd__side" @tap="propertyDialogClose">
						<a class="weui-icon-btn">返回<i class="weui-icon-back-arrow-thin"></i></a>
					</view>
					<view class="weui-half-screen-dialog__hd__main">
						<strong class="weui-half-screen-dialog__title">{{propertyDialog.mainTitle}}</strong>
						<view class="weui-half-screen-dialog__subtitle">{{propertyDialog.subTitle}}</view>
					</view>
					<view class="weui-half-screen-dialog__hd__side">
						<!-- <a class="weui-icon-btn">更多<i class="weui-icon-more"></i></a> -->
					</view>
				</view>
				<view class="weui-half-screen-dialog__bd" style="padding-bottom: 4px;">
					<view class="weui-half-screen-dialog__desc">
						{{propertyDialog.mainTip}}
					</view>

					<view class="pm-picBox">
						<image class="pm-product-image" style="width:270px;height:270px;margin:4px;"
							v-if="ZCTProductURL" :src="ZCTProductURL" mode="aspectFill"></image>
					</view>

					<view v-if="propertyDialog.subTip" class="pm-half-screen-dialog__tips" style="padding-top:0px">
						{{propertyDialog.subTip}}
					</view>

				</view>


				<scroll-view v-if="propertyDialog.subInfoList" class="pm-scroll-view_V" enable-flex="true"
					scroll-y="true" style="height: 300px;">

					<view class="page__bd" style="width:100%;">
						<view class="weui-cells" style="margin-top:2px;">
							<block v-for="(subInfo, k) in propertyDialog.subInfoList" :key="k">
								<view class="weui-cell weui-cell_active">
									<view class="weui-cell__hd" style="position: relative; margin-right: 10px;">
										<image v-if="subInfo.ZCTProductURL" :src="subInfo.ZCTProductURL"
											style="width:45px; height:45px; display:block;"></image>
									</view>
									<view class="weui-cell__bd">
										<view>{{subInfo.mainInfo}}</view>
										<view style="font-size: 13px; color: #888;">{{subInfo.subInfo}}</view>
									</view>
								</view>
							</block>
						</view>
					</view>


				</scroll-view>

				<view class="pm-half-screen-dialog__ft">
					<button :class="propertyDialog.CancelBthClass"
						@tap="propertyDialogClose">{{propertyDialog.subOperation}}</button>
					<button :class="propertyDialog.EditBtnClass" @tap="onPMCOIEdit"
						:id="propertyDialog._id">{{propertyDialog.mainOperation}}</button>
				</view>
			</view>
		</view>

		<view v-if="topTips" :class="'weui-toptips weui-toptips_warn fadeIn ' + (hide ? 'fadeOut' : '')">
			{{topTipsStr}}
		</view>

		<view v-if="showIOSDialog.isShowIOSDialog" class="fadeIn" :id="showIOSDialog._id"
			@tap="onPMCOICommandSelectClose">
			<view class="weui-mask"></view>
			<view class="weui-actionsheet weui-actionsheet_toggle">
				<view class="weui-actionsheet__title">
					<view class="weui-actionsheet__title-text">编辑 查看 分享 发布</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	//var base64 = require("../images/base64");
	const app = getApp();
	const btnSelectedStyle = " box-shadow: 2px 2px 5px #000;"; //用于动态表达按钮选中的状态
	//用于动态表达按钮选中的状态
	const btnUnSelectedStyle = " box-shadow: 0px 0px 0px #000;"; //用于动态表达按钮没有选中的状态
	//用于动态表达按钮没有选中的状态
	const pmJSTools = require("../../utils/pmJSTools.js");
	const pmSerTools = require("../../utils/pmSerTools.js");
	const DataListSkip = 0; //页起始点 （分页显示，第一页） 当系统下拉刷新时恢复到默认值
	//页起始点 （分页显示，第一页） 当系统下拉刷新时恢复到默认值
	const DataListLimit = 10; //页长 （一次从服务器获取数据项数） 当系统下拉刷新时恢复到默认值

	export default {
		data() {
			return {
				mainTitle: "我发布的任务",
				subTips: "显示及操作",
				topTips: false,
				topTipsStr: "",
				pmDialog: false,
				dialogMainTitle: "",
				dialogMainContent: "",
				dialogSubOperation: "",
				dialogMainOperation: "",
				dialogPMOrderProType: "",
				PMCOIList: [],
				//PMCOI清单
				PMCOIListSkip: 0,
				//页号 （分页显示，第一页） //下拉初始化， 上拉加页号
				PMCOIListLimit: DataListLimit,
				//页长 （一次从服务器获取数据项数） //下拉初始化， 上拉加页长不变
				isMoveToDown: false,
				//是否上滑到底部，滑到底部进行加载更多
				isMoveToUp: false,
				//是否下滑到顶部，滑到顶部进行加载更新(默认是true)
				mHeight: 1000,
				//设置一个默认屏高参数
				showIOSDialog: {
					//PMCOI命令对话框
					isShowIOSDialog: false,
					_id: "" //对象主键

				},
				propertyDialog: {
					//属性框数据
					isPropertyDialogOpen: false,
					//是否打开半屏组件
					mainTitle: "主标题",
					//主标题
					subTitle: "子标题",
					//子标题
					mainTip: "主提示",
					//主提示
					subTip: "子提示",
					//子提示
					subOperation: "取消",
					//辅助操作按钮提示
					mainOperation: "确认",
					//主操作按钮提示
					_id: "",
					//对象主键
					ZCTProductURL: "",
					//主对象图片
					subInfoList: [{
						mainInfo: "",
						//属性类型
						subInfo: "",
						//属性描述
						//  ZCTProductURL: "" //产品图片

					}]
				},
				classView: "weui-actionsheet__cell",
				//长按交付任务弹出编辑、删除按钮默认可以操作；授权后灰色不可操作
				ClickItemData: {},
				//任务清单点击项的数据源。用于控制操作按钮的显示
				OptionSelectState: ['ALLOWED'],
				//默认查询执行中的任务
				OptionSelectStateName: "执行中",
				// topTips: false,
				// //顶部提示显示开关
				// topTipsStr: "",
				// //顶部提示内容
				ICOImage: {
					ICOImageEdit: "/static/images/icon/ico_Edit_C.png",
					ICOImagePublish: "/static/images/icon/ico_PUBLISH.png",
					ICOImageWaitAuthorize: "/static/images/icon/ico_WaitAuthorize.png",
					ICOImageEXEC: "/static/images/icon/ico_EXEC.png",
					ICOImageUnaccepted: "/static/images/icon/ico_UNACCEPTED.png",
					ICOImageAccept: "/static/images/icon/ico_ACCEPT.png"
				},
				pmDialogShouQuan: false,
				COIAddState: true, //交付任务创建按钮操作状态,
				// ZCTProductURL: "",
				hide: false,
				theme: "",
				btnCommandName_id: ""
			};
		},
		onLoad: async function(options) {
			//用来给scrollview设置固定高度
			var that = this;
			uni.getSystemInfo({
				success: function(res) {
					that.mHeight = res.windowHeight
				}
			})
			uniCloud.callFunction({
				name: "test",
				data: {
				},
				success(res) {
					console.log(res)
				},
				fail() {
			
				}
			})
		},
		methods: {}
	}
</script>
<style>
	//@import "./taskList.css";
	.weui-loading__wrp {
		padding: 16px;
		text-align: center
	}

	.weui-loading__wrp:last-child {
		background-color: rgba(0, 0, 0, .1)
	}

	.loadmore {
		width: 100%;
		height: 50px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
	}

	.weui-toptips {
		display: block
	}

	.weui-actionsheet__cellnone {
		position: relative;
		padding: 16px;
		text-align: center;
		font-size: 17px;
		line-height: 1.41176471;
		color: var(--weui-FG-3);
		pointer-events: none;
	}

	.ColumnBox {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	.RowBox {
		display: flex;
		flex-direction: Row;
		height: 100%;
	}

	.task_progressGroup {
		display: flex;
		flex-direction: row;
		height: 5px;
		width: 100%;
		margin-bottom: 10px;

	}

	.task_progress {
		height: 15px;
		width: 20%;
	}

	.task_progressSub {
		height: 5px;
		width: 100%;
		background-color: lightgreen;
		border-style: solid;
		border-color: gray;
		border-width: thin;
		border-radius: 5px;
	}

	.task_progressSubTxt {
		display: block;
		color: var(--weui-FG-1);
		font-size: 10px;
		text-align: center;
	}

	.stateselect_panel {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 90%;
		margin: 0px auto;
	}

	.stateselect_item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
	}

	.taskdetail_title {
		display: flex;
		flex-direction: row;
		/* align-items:flex-end */
	}

	.taskName {
		font-weight: 400;
		font-size: 17px;
		color: var(--weui-FG-0);
		width: auto;
		word-wrap: normal;
		word-wrap: break-word;
		word-break: break-all
	}

	.taskNum {
		font-weight: 400;
		font-size: 17px;
		color: #996633;
		/* width: auto; */
		min-width: 140rpx;
		word-wrap: normal;
		word-wrap: break-word;
		word-break: break-all;
		margin-left: 20rpx;
	}
</style>
